import React from "react";
// import React, { useRef, useState } from "react";
// Import Swiper React components
import { Swiper, SwiperSlide } from "swiper/react";
import { Autoplay, Pagination, Navigation } from "swiper";

// Import Swiper styles
import "swiper/css";
import "swiper/css/pagination";
import "swiper/css/navigation";

const Slider = () => {
  return (
    <div className="slider_container">
      <div className="elipse" id="upper_elipse"></div>
      <Swiper
        spaceBetween={30}
        centeredSlides={true}
        loop={true}
        speed={1000}
        autoplay={{
          delay: 3000,
          disableOnInteraction: false,
        }}
        pagination={{
          clickable: true,
          // dynamicBullets: true,
        }}
        navigation={true}
        modules={[Autoplay, Pagination, Navigation]}
        className="mySwiper"
      >
        <SwiperSlide>
          <div className="slide">
            <img src={require("../assets/image 1.png")} alt="" srcset="" />
          </div>
        </SwiperSlide>
        <SwiperSlide>
          <div className="slide">
            <img src={require("../assets/image 1.png")} alt="" srcset="" />
          </div>
        </SwiperSlide>
        <SwiperSlide>
          <div className="slide">
            <img src={require("../assets/image 2.png")} alt="" srcset="" />
          </div>
        </SwiperSlide>
        <SwiperSlide>
          <div className="slide">
            <img src={require("../assets/image 3.png")} alt="" srcset="" />
          </div>
        </SwiperSlide>
        <SwiperSlide>
          <div className="slide">
            <img src={require("../assets/image 1.png")} alt="" srcset="" />
          </div>
        </SwiperSlide>
        {/* <SwiperSlide><img src={require("../assets/image 1.png")} alt="" srcset="" /></SwiperSlide>
        <SwiperSlide>Slide 7</SwiperSlide>
        <SwiperSlide>Slide 8</SwiperSlide>
        <SwiperSlide>Slide 9</SwiperSlide> */}
      </Swiper>

      <div className="elipse" id="lower_elipse"></div>
    </div>
  );
};

export default Slider;
