import React from "react";
import { Swiper, SwiperSlide } from "swiper/react";

// Import Swiper styles
import "swiper/css";
import "swiper/css/pagination";
import "swiper/css/navigation";
import "swiper/css/effect-fade";

// import required modules
import { EffectFade, Pagination, Navigation } from "swiper";

const ImageSlider = () => {
  return (
    <div className="image_slider">
      <Swiper
        slidesPerView={1}
        effect={"fade"}
        spaceBetween={30}
        loop={true}
        pagination={{
          clickable: true,
        }}
        navigation={true}
        modules={[EffectFade, Pagination, Navigation]}
        className="mySwiper"
      >
        <SwiperSlide>
          <div className="img">
            <div className="gradient"></div>
            <img
              src={require("../assets/places/Rishikesh.png")}
              alt=""
              srcset=""
            />
          </div>
        </SwiperSlide>
        <SwiperSlide>
          <div className="img">
            <img
              src={require("../assets/places/Haridwar.png")}
              alt=""
              srcset=""
            />
          </div>
        </SwiperSlide>
        <SwiperSlide>
          <div className="img">
            <img
              src={require("../assets/places/Mussoorie.png")}
              alt=""
              srcset=""
            />
          </div>
        </SwiperSlide>
      </Swiper>
    </div>
  );
};

export default ImageSlider;
