import React from "react";
import { Link } from "react-router-dom";

const Menubar = () => {
  return (
    <div className="Menubar">
      <ul className="container">
        <Link to="/Home">
          <li className="list-items">Home</li>
        </Link>
        <Link to="/Theme">
          <li className="list-items">Theme</li>
        </Link>
        <Link to="/org-comittee">
          <li className="list-items">Organizing Commitee</li>
        </Link>
        <li className="list-items">Advisory Commitee</li>
        <li className="list-items">Guest speakers</li>
        <li className="list-items">Contact us</li>
      </ul>
    </div>
  );
};

export default Menubar;
